<template>
  <view class="admin-container">
    <!-- 顶部：搜索 + 用户信息 -->
    <view class="admin-topbar">
      <view class="search-wrap">
        <input class="search-input" type="text" v-model="keyword" placeholder="Search" confirm-type="search" @confirm="onSearch" />
        <button class="search-btn" @click="onSearch">搜索</button>
      </view>
      <view class="user-wrap" @click="goProfile">
        <image class="avatar" :src="userInfo.avatar_url || defaultAvatar" mode="cover" />
        <view class="user-meta">
          <text class="user-name">{{ userInfo.nickname || userInfo.username || 'Admin' }}</text>
          <text class="user-role">{{ userInfo.role || 'admin' }}</text>
        </view>
      </view>
    </view>

    <view class="admin-header">
      <text class="admin-title">管理后台</text>
      <text class="admin-subtitle">系统管理控制台</text>
    </view>

    <!-- 统计卡片 -->
    <view class="stats-grid">
      <view class="stat-card">
        <text class="stat-number">{{ stats.userCount || 0 }}</text>
        <text class="stat-label">用户总数</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ stats.productCount || 0 }}</text>
        <text class="stat-label">商品总数</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">{{ stats.orderCount || 0 }}</text>
        <text class="stat-label">订单总数</text>
      </view>
      <view class="stat-card">
        <text class="stat-number">¥{{ stats.totalSales || 0 }}</text>
        <text class="stat-label">总销售额</text>
      </view>
    </view>

    <!-- 功能菜单 -->
    <view class="menu-section">
      <text class="section-title">系统管理</text>
      <view class="menu-grid">
        <view class="menu-item" @click="goToUserManage">
          <text class="menu-icon">👥</text>
          <text class="menu-title">用户管理</text>
          <text class="menu-desc">管理用户账户</text>
        </view>

        <view class="menu-item" @click="goToProductManage">
          <text class="menu-icon">📦</text>
          <text class="menu-title">商品管理</text>
          <text class="menu-desc">管理商品信息</text>
        </view>

        <view class="menu-item" @click="goToOrderManage">
          <text class="menu-icon">📋</text>
          <text class="menu-title">订单管理</text>
          <text class="menu-desc">查看订单状态</text>
        </view>

        <view class="menu-item" @click="showCreateUserModal">
          <text class="menu-icon">➕</text>
          <text class="menu-title">创建用户</text>
          <text class="menu-desc">添加新用户</text>
        </view>
      </view>
    </view>

    <!-- 创建用户模态框 -->
    <view class="modal-overlay" v-if="showModal" @click="hideModal">
      <view class="modal-content" @click.stop>
        <view class="modal-header">
          <text class="modal-title">创建新用户</text>
          <text class="modal-close" @click="hideModal">×</text>
        </view>

        <view class="modal-body">
          <view class="form-group">
            <text class="form-label">用户名</text>
            <input type="text" v-model="newUser.username" placeholder="请输入用户名" class="form-input" />
          </view>

          <view class="form-group">
            <text class="form-label">密码</text>
            <input type="password" v-model="newUser.password" placeholder="请输入密码" class="form-input" />
          </view>

          <view class="form-group">
            <text class="form-label">用户角色</text>
            <picker :value="roleIndex" :range="roleOptions" @change="onRoleChange" class="form-picker">
              <view class="picker-text">{{ roleOptions[roleIndex] }}</view>
            </picker>
          </view>
        </view>

        <view class="modal-footer">
          <button @click="hideModal" class="btn-cancel">取消</button>
          <button @click="createUser" class="btn-confirm" :disabled="creating">
            {{ creating ? '创建中...' : '创建' }}
          </button>
        </view>
      </view>
    </view>

    <!-- 榜单区域：热卖商品 / 热卖品牌 / 热卖分类 -->
    <view class="boards-grid">
      <view class="board-card">
        <view class="board-header"><text class="board-title">本周热卖商品</text></view>
        <view class="board-body">
          <view class="table-row table-head">
            <text class="col col-id">产品ID</text>
            <text class="col col-name">产品名称</text>
            <text class="col col-num">周销量</text>
            <text class="col col-num">周GMV</text>
          </view>
          <view class="table-row" v-for="(item, idx) in hotProducts" :key="idx">
            <text class="col col-id">{{ item.id }}</text>
            <text class="col col-name ellipsis">{{ item.name }}</text>
            <text class="col col-num">{{ item.weekSales }}</text>
            <text class="col col-num">{{ item.weekGMV }}</text>
          </view>
          <view class="empty" v-if="!hotProducts.length">暂无数据</view>
        </view>
      </view>

      <view class="board-card">
        <view class="board-header"><text class="board-title">本周热卖品牌</text></view>
        <view class="board-body">
          <view class="table-row table-head">
            <text class="col col-id">品牌ID</text>
            <text class="col col-name">品牌名称</text>
            <text class="col col-num">周销量</text>
            <text class="col col-num">周GMV</text>
          </view>
          <view class="table-row" v-for="(item, idx) in hotBrands" :key="idx">
            <text class="col col-id">{{ item.id }}</text>
            <text class="col col-name ellipsis">{{ item.name }}</text>
            <text class="col col-num">{{ item.weekSales }}</text>
            <text class="col col-num">{{ item.weekGMV }}</text>
          </view>
          <view class="empty" v-if="!hotBrands.length">暂无数据</view>
        </view>
      </view>

      <view class="board-card">
        <view class="board-header"><text class="board-title">本周热卖分类</text></view>
        <view class="board-body">
          <view class="table-row table-head">
            <text class="col col-id">分类ID</text>
            <text class="col col-name">分类名称</text>
            <text class="col col-num">周销量</text>
            <text class="col col-num">周GMV</text>
          </view>
          <view class="table-row" v-for="(item, idx) in hotCategories" :key="idx">
            <text class="col col-id">{{ item.id }}</text>
            <text class="col col-name ellipsis">{{ item.name }}</text>
            <text class="col col-num">{{ item.weekSales }}</text>
            <text class="col col-num">{{ item.weekGMV }}</text>
          </view>
          <view class="empty" v-if="!hotCategories.length">暂无数据</view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      keyword: '',
      userInfo: uni.getStorageSync('userInfo') || {},
      defaultAvatar: '/static/logo.png',
      stats: {},
      showModal: false,
      creating: false,
      newUser: { username: '', password: '', role: 'customer' },
      roleOptions: ['普通用户', '管理员'],
      roleIndex: 0,
      hotProducts: [],
      hotBrands: [],
      hotCategories: []
    };
  },
  onLoad() {
    this.loadStats();
    this.loadBoards();
  },
  methods: {
    onSearch() {
      uni.showToast({ title: this.keyword ? '搜索：' + this.keyword : '请输入关键词', icon: this.keyword ? 'none' : 'none' });
    },
    goProfile() {
      uni.navigateTo({ url: '/pages/account/profile' });
    },
    async loadStats() {
      try {
        // 使用新的统计接口
        const res = await this.$api.statistics.getSystemStats();
        const r = res[1] || res;
        if (r && r.statusCode >= 200 && r.statusCode < 300) {
          this.stats = r.data || {};
        } else {
          uni.showToast({ title: r?.data?.error || '加载统计失败', icon: 'none' });
        }
      } catch (e) {
        console.error('加载统计数据失败:', e);
        // 如果API失败，使用默认数据
        this.stats = {
          userCount: 0,
          productCount: 0,
          orderCount: 0,
          totalSales: 0
        };
      }
    },
    async loadBoards() {
      try {
        // 并行获取所有统计数据
        const [hotProductsRes, hotCategoriesRes, hotBrandsRes] = await Promise.all([
          this.$api.statistics.getHotProducts({ period: 'week', limit: 5 }),
          this.$api.statistics.getHotCategories({ period: 'week', limit: 5 }),
          this.$api.brands.getBrandStats({ period: 'week' })
        ]);
        
        // 处理热卖商品数据
        const hotProducts = hotProductsRes[1]?.data || hotProductsRes?.data || [];
        this.hotProducts = hotProducts;
        
        // 处理热卖分类数据
        const hotCategories = hotCategoriesRes[1]?.data || hotCategoriesRes?.data || [];
        this.hotCategories = hotCategories;
        
        // 处理热卖品牌数据
        const hotBrands = hotBrandsRes[1]?.data || hotBrandsRes?.data || [];
        this.hotBrands = hotBrands;
        
      } catch (e) {
        console.error('加载榜单失败:', e);
        // 如果API失败，使用默认数据
        this.hotProducts = [];
        this.hotBrands = [];
        this.hotCategories = [];
      }
    },

    goToUserManage() { uni.navigateTo({ url: '/pages/management/user-manage' }); },
    goToProductManage() { uni.navigateTo({ url: '/pages/management/product-manage' }); },
    goToOrderManage() { uni.navigateTo({ url: '/pages/management/order-manage' }); },

    showCreateUserModal() { this.showModal = true; },
    hideModal() {
      this.showModal = false;
      this.newUser = { username: '', password: '', role: 'customer' };
      this.roleIndex = 0;
    },
    onRoleChange(e) {
      this.roleIndex = Number(e.detail.value || 0);
      this.newUser.role = this.roleIndex === 0 ? 'customer' : 'admin';
    },

    async createUser() {
      if (!this.newUser.username.trim() || !this.newUser.password) {
        uni.showToast({ title: '请填写完整信息', icon: 'none' });
        return;
      }
      this.creating = true;
      try {
        // 走统一 API 封装（自动带 token）
        const res = await this.$api.admin.createUser(this.newUser);
        const r = res[1] || res;
        if (r && r.statusCode >= 200 && r.statusCode < 300) {
          uni.showToast({ title: '用户创建成功', icon: 'success' });
          this.hideModal();
          this.loadStats();
        } else {
          uni.showToast({ title: r?.data?.error || '创建失败', icon: 'none' });
        }
      } catch (e) {
        console.error('创建用户失败:', e);
        uni.showToast({ title: '网络错误，请重试', icon: 'none' });
      } finally {
        this.creating = false;
      }
    }
  }
};
</script>

<style scoped>
