  flex: 1;
  display: flex;
  align-items: center;
  gap: 16rpx;
  background: #ffffff;
  border-radius: 999rpx;
  padding: 10rpx 20rpx;
  box-shadow: 0 4rpx 20rpx rgba(0,0,0,0.06);
}

.search-input {
  flex: 1;
  height: 64rpx;
  padding: 0 10rpx;
  font-size: 28rpx;
}

