  display: flex;
  align-items: center;
  gap: 16rpx;
  background: #ffffff;
  padding: 10rpx 16rpx;
  border-radius: 999rpx;
  box-shadow: 0 4rpx 20rpx rgba(0,0,0,0.06);
}

