  text-align: center;
  margin-bottom: 60rpx;
}

.admin-title {
  font-size: 48rpx;
  font-weight: bold;
  color: #333;
  display: block;
  margin-bottom: 20rpx;
}

.admin-subtitle {
  font-size: 28rpx;
  color: #666;
}

.stats-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30rpx;
  margin-bottom: 60rpx;
}

.stat-card {
  background: white;
  border-radius: 20rpx;
  padding: 40rpx;
  text-align: center;
  box-shadow: 0 4rpx 20rpx rgba(0,0,0,0.1);
}

.stat-number {
  font-size: 48rpx;
  font-weight: bold;
  color: #3cc51f;
