}

.menu-desc {
  font-size: 24rpx;
  color: #666;
}

/* 榜单区域 */
.boards-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30rpx;
}

.board-card {
  background: #fff;
  border-radius: 20rpx;
  box-shadow: 0 4rpx 20rpx rgba(0,0,0,0.06);
}

.board-header { padding: 24rpx 28rpx; border-bottom: 2rpx solid #f0f0f0; }
.board-title { font-size: 30rpx; color: #333; font-weight: 600; }
.board-body { padding: 10rpx 0; }

.table-row { display: grid; grid-template-columns: 160rpx 1fr 160rpx 180rpx; padding: 20rpx 28rpx; align-items: center; }
.table-head { background: #fafafa; color: #666; font-size: 24rpx; }
.col { font-size: 26rpx; color: #333; }
.col-id { color: #666; }
.col-num { text-align: right; }
.ellipsis { overflow: hidden; white-space: nowrap; text-overflow: ellipsis; }
.empty { padding: 24rpx 28rpx; color: #999; font-size: 24rpx; }

/* 模态框样式 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 20rpx;
  width: 80%;
  max-width: 600rpx;
}

.modal-header {
  padding: 40rpx;
  border-bottom: 2rpx solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-title {
  font-size: 36rpx;
  font-weight: bold;
  color: #333;
}

.modal-close {
  font-size: 48rpx;
  color: #999;
  cursor: pointer;
}

.modal-body {
  padding: 40rpx;
}

.form-group {
  margin-bottom: 40rpx;
}

.form-label {
  display: block;
  font-size: 28rpx;
  color: #333;
  margin-bottom: 20rpx;
  font-weight: 500;
}

.form-input {
  width: 100%;
  height: 80rpx;
  border: 2rpx solid #e0e0e0;
